.header {
  display: flex;
  flex-direction: column;
}

.header-title {
  display: flex;
  position: fixed;
  width: 100%;
  background-color: rgb(0, 20, 64);
  color: white;
  padding: 10px 20px 10px 20px;
  z-index: 2;
}

.header-title p {
  font-size: 1.5em;
  font-family: 'Barlow', sans-serif;
  padding: 10px;
  margin: 0px;
}

.header-buttons {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 40px;
  flex: 1;
}

.header-buttons p {
  margin: 0px 10px 0px 10px;
}

.header-buttons p:hover {
  cursor: pointer;
  color: rgb(254, 234, 0);
}

.footer {
  width: 100%;
  height: 40px;
  background-color: rgb(0, 20, 64);
  display: flex;
  align-items: center;
  margin: 0px;
}

.footer p {
  font-family: 'Barlow', sans-serif;
  padding: 10px;
  font-size: 15px;
  color: white;
}

@media only screen and (max-width: 800px) {
  .header-title p {
      padding: 10px 0px 10px 0px;
      font-size: 0.75em;
  }
}

